import './App.css';
import Navbar from "./components/Navbar/Navbar";
import {BrowserRouter, Route} from 'react-router-dom'
import UsersContainer from "./components/Users/UsersContainer";
import ProfileContainer from "./components/Profile/ProfileContainer";
import HeaderContainer from "./components/Header/HeaderContainer";
import LoginPage from "./components/Login/Login";
import React, {Component} from "react";
import {connect, Provider} from "react-redux";
import {initializeApp} from "./redux/app-reducer";
import Preloader from "./components/Common/Preloader/Preloader";
import store from "./redux/redux-store";
import {withSuspence} from "./hoc/withSuspence";

const DialogsContainer = React.lazy(() => import('./components/Navbar/Dialogs/DialogsContainer'));
const Music = React.lazy(() => import('./components/Navbar/Music/Music'));
const News = React.lazy(() => import('./components/Navbar/News/News'));
const Settings = React.lazy(() => import('./components/Navbar/Settings/Settings'));


class App extends Component {

    componentDidMount() {
        this.props.initializeApp()
    }

    render() {
        if(!this.props.initialized){
            return <Preloader/>
        }

        return (

            <div className='app-wrapper'>
                <HeaderContainer/>
                <Navbar/>

                <div className='app-wrapper-content'>
                    <Route path='/profile/:userId?' render={() => <ProfileContainer/>}/>
                    <Route path='/dialogs' render={withSuspence(DialogsContainer)}/>
                    <Route path='/music' render={withSuspence(Music)}/>

                    <Route path='/news' render={withSuspence(News)}/>
                    <Route path='/settings' render={withSuspence(Settings)}/>

                    <Route path='/users' render={() => <UsersContainer/>}/>
                    <Route path='/login' render={() => <LoginPage/>}/>
                </div>
            </div>

        );

    }
}

const mapStateToProps = (state) => ({
    initialized : state.app.initialized
})

let AppContainer = connect (mapStateToProps,{initializeApp}) (App)

let SamuraiJsApp = (props) => {
    return <BrowserRouter>

        <Provider store={store}>
            <AppContainer/>
        </Provider>

    </BrowserRouter>
}
export default SamuraiJsApp
