const ADD_MESSAGE = 'ADD-MESSAGE'


let initialState = {
    dialogArray :  [
        {id : 1, name : 'Vladislav'},
        {id : 2, name : 'Nikita'},
        {id : 3, name : 'Kira'},
        {id : 4, name : 'Mix'},
        {id : 5, name : 'Roma'}
    ],
    messageArray : [
        {id : 1, message : 'Hello,how are you?'},
        {id : 2, message : 'How are you?'},
        {id : 3, message : 'May be go for a walk?'}
    ]
}

const messagesReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MESSAGE :
            let newMes = action.newMessageElement
            return  {
                ...state,
                messageArray : [...state.messageArray, {id :4, message : newMes}],

            }



        default:
            return state
    }
}


export const addMessageCreator = (newMessageElement) => ({type : ADD_MESSAGE, newMessageElement})


export default messagesReducer