import * as yup from "yup";
import {Field, Form, Formik} from "formik";
import s from "../Navbar/Dialogs/Dialogs.module.css";

export const LoginPage = ({onSubmit}) => {

    const validationSchema = yup.object().shape({
        email : yup.string()
            .required('Need This Field'),
        password : yup.string()
            .required('Need This Field')
    })

    return (
        <div>

            <Formik
                initialValues={{
                    email: '',
                    password: '',
                    rememberMe: false
                }}

                onSubmit={(values, {setSubmitting}) => {

                    onSubmit(values);
                    setSubmitting(false);

                }}
                validationSchema={validationSchema}
            >
                {({
                      values, errors, touched,
                      handleChange, handleBlur,
                      isValid, handleSubmit, dirty
                  }) => (
                    <Form>
                        <div>
                            <Field type='text' name='email' placeholder={'email'}
                                   onChange={handleChange} onBlur={handleBlur} value={values.login}/>
                        </div>

                        <div>
                            {touched.email && errors.email && <p className={s.error}>{errors.email}</p>}
                        </div>

                        <div>
                            <Field type="password" name="password" placeholder={'password'}
                                   onChange={handleChange} onBlur={handleBlur} value={values.password}/>
                        </div>

                        <div>
                            {touched.password && errors.password && <p className={s.error}>{errors.password}</p>}
                        </div>

                        <div>
                            <Field type="checkbox" name="rememberMe" /> Remember me
                        </div>
                        <div>
                            <button type="submit" disabled={!isValid && !dirty}>
                                Log in
                            </button>
                        </div>
                    </Form>
                )}
            </Formik>
        </div>
    )
};