import s from './Myposts.module.css'
import Post from "./Post/Post";
import * as React from "react";
import {Formik, Form, Field} from 'formik'
import * as yup from 'yup'




const Myposts = React.memo(props => {

    let posts = [...props.posts]
        .reverse()
        .map(p => <Post message={p.message} key={p.message} likesCount={p.likesCount}/>)

    let addPostChange = (values) => {
        props.addPost(values.newPostElement)
    }

    return (
        <div className={s.posts}>
            <div className={s.myPosts}>
                <h3>Myposts</h3>
            </div>


            <MyPostForm onSubmit={addPostChange}/>
            {posts}

        </div>
    )
})

const MyPostForm = (props) => {

    const validationSchema = yup.object().shape({
        newPostElement : yup.string()
            .min(4, 'Too short')
            .max(20, 'Too long')
            .required('Need Field')
    })

    return (
        <Formik
            initialValues={{newPostElement: ''}}



            onSubmit={(values, {setSubmitting}) => {
                props.onSubmit(values)
                setSubmitting(false)
            }}
            validationSchema={validationSchema}

        >
            {({
                  values, errors, touched,
                  handleChange, handleBlur,
                  isValid, handleSubmit, dirty
            }) => (
                <Form>
                    <div>
                        <Field type='text' name={'newPostElement'} placeholder={"New Post"}
                               component='textarea' onChange={handleChange} onBlur={handleBlur} value={values.newPostElement}/>

                        <div>
                            {touched.newPostElement && errors.newPostElement && <p className={s.error}>{errors.newPostElement}</p>}
                        </div>
                    </div>
                    <div>
                        <button type='submit' disabled={!isValid && !dirty}>
                            Submit
                        </button>
                    </div>

                </Form>
            )}
        </Formik>
    )
}

export default Myposts