import {ProfileAPI, UsersAPI} from "../components/API/api";

const ADD_POST = 'ADD-POST'

const SET_USER_PROFILE = 'SET_USER_PROFILE'
const SET_STATUS = 'SET_STATUS'

const DELETE_POST = 'DELETE_POST'

let initialState = {
    postArray: [
        {id : 1, message: 'Hello, there very good place', likesCount: '23 likes'},
        {id : 2, message: 'Do to be best!', likesCount: '123 likes'},
        {id : 3, message: 'Nice page', likesCount: '10 likes'}
    ],

    profile : null,
    status : ''
}

const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST : {
            let newPostText = action.newPostElement
            return  {
                ...state,
                postArray : [...state.postArray,{message: newPostText, likesCount: '93 likes',id : 4}],
                newPostText : ''
            }

        }

        case SET_USER_PROFILE : {
            return {
                ...state, profile: action.profile
            }
        }
        case SET_STATUS: {
            return  {
                ...state,
                status: action.status
            }
        }

        case DELETE_POST: {
            return  {
                ...state, postArray : state.postArray.filter(p => p.id != action.userId)
            }
        }
        default :
            return state


    }
}

export const deletePost = (userId) => ({type : DELETE_POST, userId})

export const addPostCreator = (newPostElement) => ({type: ADD_POST, newPostElement})

export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile })
export const setStatus = (status) => ({type: SET_STATUS, status })

export const getUserProfile = (userId) => (dispatch) => {
    UsersAPI.getProfile(userId)
        .then(response => {
            dispatch(setUserProfile(response.data))

        })
}

export const getStatus = (userId) => (dispatch) => {
    ProfileAPI.getStatus(userId)
        .then(response => {
            dispatch(setStatus(response.data))

        })
}

export const updateStatus = (status) => (dispatch) => {
    ProfileAPI.updateStatus(status)
        .then(response => {
            if (response.data.resultCode === 0) {
                dispatch(setStatus(status))
            }

        })
}

export default profileReducer